html{
    box-sizing: border-box;
    width: 100vw;
    height: 100vh;
    max-width: 100%;
}
*, *::after *::before{
    box-sizing: inherit;
}
body{
    overflow-x: hidden;
    font-family: 'Roboto Slab', sans-serif;
    font-size: clamp(0.75rem, calc(0.5rem + 1vw),1.25rem);
    margin:0;
}

a {
    color: inherit;
    text-decoration: none;
}

h1,h2,h3,h4{
    font-family: Roboto, sans-serif;
}