.Entete {
    position: sticky;
    top:0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #444;
    color: white;
    z-index: 2;
    height: 4rem;

    h1 {
        font-size: 1.4rem;
        padding: 1rem 2rem;
    }

    nav{
        padding: 1rem 2rem;
        display: flex;
        align-items: center;
        #cc-sommaire-panier {
            display: none;
        }
        .sommaire-panier {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            font-size: 0.85rem;
            background-color: darkgreen;
            position: absolute;
            top: 4rem;
            right: 0;
            transform: translateX(100%);
            transition: transform 0.5s;

            h3 {
                margin:0;
                padding: 0.5rem 1rem;
                background-color: black;
                color: white;
            }
            & > div{
                display: flex;
                justify-content: space-between;
                padding: 0.5rem 1rem;
                border-bottom: 1px dotted white;
            }
        }
        #cc-sommaire-panier:checked ~ .sommaire-panier{
            transform: unset;
        }
        label {
            cursor: pointer;
        }
        a{
            padding-left: 0.7rem;
            font-size: 0.9rem;
            white-space: nowrap;
        }
    }
}